// LinkedIn Field Analyzer - Outil de diagnostic pour LIN-19
// Ce script aide √† identifier et tester tous les champs LinkedIn

/**
 * Analyseur de champs LinkedIn
 * Permet de diagnostiquer et tester la compatibilit√© des champs
 */
class LinkedInFieldAnalyzer {
  constructor() {
    this.foundFields = [];
    this.testedFields = [];
    this.isAnalyzing = false;
  }

  /**
   * Lance l'analyse compl√®te des champs sur la page actuelle
   */
  async analyzeCurrentPage() {
    if (this.isAnalyzing) {
      console.log('üîÑ Analyse d√©j√† en cours...');
      return;
    }

    console.log('üîç D√©but de l\'analyse des champs LinkedIn...');
    this.isAnalyzing = true;
    this.foundFields = [];

    try {
      // Analyser tous les s√©lecteurs connus
      await this.scanKnownSelectors();
      
      // Rechercher de nouveaux champs potentiels
      await this.discoverNewFields();
      
      // G√©n√©rer le rapport
      this.generateReport();
      
    } catch (error) {
      console.error('‚ùå Erreur lors de l\'analyse:', error);
    } finally {
      this.isAnalyzing = false;
    }
  }

  /**
   * Scanne les s√©lecteurs d√©j√† connus
   */
  async scanKnownSelectors() {
    const knownSelectors = [
      // Posts
      { selector: '[data-placeholder*="partager"]', type: 'post', description: 'Post fran√ßais' },
      { selector: '[data-placeholder*="What\'s"]', type: 'post', description: 'Post anglais' },
      { selector: '.ql-editor[contenteditable="true"]', type: 'post', description: '√âditeur principal' },
      { selector: '[role="textbox"]', type: 'post', description: 'Textbox ARIA' },
      
      // Commentaires
      { selector: '[data-placeholder*="comment"]', type: 'comment', description: 'Commentaire g√©n√©ral' },
      { selector: '[data-placeholder*="Add a comment"]', type: 'comment', description: 'Commentaire anglais' },
      { selector: '.comments-comment-box__form textarea', type: 'comment', description: 'Form commentaire' },
      { selector: '.comments-comment-texteditor', type: 'comment', description: '√âditeur commentaire' },
      
      // Messages
      { selector: '[data-placeholder*="message"]', type: 'message', description: 'Message g√©n√©ral' },
      { selector: '[data-placeholder*="Write a message"]', type: 'message', description: 'Message anglais' },
      { selector: '.msg-form__contenteditable', type: 'message', description: 'Form message' },
      { selector: '.msg-form__compose', type: 'message', description: 'Compose message' },
      
      // Profil
      { selector: '[data-placeholder*="headline"]', type: 'profile', description: 'Titre profil' },
      { selector: '[data-placeholder*="summary"]', type: 'profile', description: 'R√©sum√© profil' },
      { selector: 'textarea[name*="summary"]', type: 'profile', description: 'Textarea r√©sum√©' },
      { selector: 'textarea[name*="description"]', type: 'profile', description: 'Textarea description' }
    ];

    console.log('üîç Scan des s√©lecteurs connus...');
    
    for (const config of knownSelectors) {
      try {
        const elements = document.querySelectorAll(config.selector);
        if (elements.length > 0) {
          elements.forEach((element, index) => {
            this.foundFields.push({
              ...config,
              element: element,
              index: index,
              isVisible: this.isElementVisible(element),
              boundingRect: element.getBoundingClientRect(),
              computedStyle: window.getComputedStyle(element)
            });
          });
          console.log(`‚úÖ ${config.description}: ${elements.length} √©l√©ment(s) trouv√©(s)`);
        }
      } catch (error) {
        console.warn(`‚ö†Ô∏è Erreur avec s√©lecteur ${config.selector}:`, error);
      }
    }
  }

  /**
   * D√©couvre de nouveaux champs potentiels
   */
  async discoverNewFields() {
    console.log('üîç Recherche de nouveaux champs...');
    
    // Chercher tous les √©l√©ments contenteditable
    const contentEditables = document.querySelectorAll('[contenteditable="true"]');
    console.log(`üìù ${contentEditables.length} √©l√©ments contenteditable trouv√©s`);
    
    // Chercher toutes les textareas
    const textareas = document.querySelectorAll('textarea');
    console.log(`üìù ${textareas.length} textareas trouv√©es`);
    
    // Chercher tous les inputs text
    const textInputs = document.querySelectorAll('input[type="text"]');
    console.log(`üìù ${textInputs.length} inputs text trouv√©s`);
    
    // Chercher les √©l√©ments avec role="textbox"
    const textboxes = document.querySelectorAll('[role="textbox"]');
    console.log(`üìù ${textboxes.length} √©l√©ments textbox trouv√©s`);

    // Analyser les nouveaux √©l√©ments
    const allCandidates = [...contentEditables, ...textareas, ...textInputs, ...textboxes];
    
    for (const element of allCandidates) {
      if (!this.isAlreadyKnown(element)) {
        this.foundFields.push({
          selector: this.generateSelector(element),
          type: 'unknown',
          description: 'Nouveau champ d√©couvert',
          element: element,
          isVisible: this.isElementVisible(element),
          boundingRect: element.getBoundingClientRect(),
          placeholder: element.getAttribute('data-placeholder') || element.placeholder || '',
          className: element.className,
          id: element.id
        });
      }
    }
  }

  /**
   * V√©rifie si un √©l√©ment est d√©j√† connu
   */
  isAlreadyKnown(element) {
    return this.foundFields.some(field => field.element === element);
  }

  /**
   * G√©n√®re un s√©lecteur CSS pour un √©l√©ment
   */
  generateSelector(element) {
    if (element.id) {
      return `#${element.id}`;
    }
    
    if (element.className) {
      const classes = element.className.split(' ').filter(c => c.length > 0);
      if (classes.length > 0) {
        return `.${classes[0]}`;
      }
    }
    
    return element.tagName.toLowerCase();
  }

  /**
   * V√©rifie si un √©l√©ment est visible
   */
  isElementVisible(element) {
    const rect = element.getBoundingClientRect();
    const style = window.getComputedStyle(element);
    
    return (
      rect.width > 0 &&
      rect.height > 0 &&
      style.display !== 'none' &&
      style.visibility !== 'hidden' &&
      style.opacity !== '0'
    );
  }

  /**
   * Teste la s√©lection sur un champ sp√©cifique
   */
  async testFieldSelection(fieldData) {
    console.log(`üß™ Test de s√©lection sur: ${fieldData.description}`);
    
    try {
      const element = fieldData.element;
      
      // Focus sur l'√©l√©ment
      element.focus();
      
      // Ins√©rer du texte de test
      const testText = 'Test de s√©lection LinkedIn Formateur';
      
      if (element.contentEditable === 'true') {
        element.textContent = testText;
      } else if (element.tagName === 'TEXTAREA' || element.tagName === 'INPUT') {
        element.value = testText;
      }
      
      // S√©lectionner le texte
      this.selectAllText(element);
      
      // D√©clencher l'√©v√©nement de s√©lection
      const selectionEvent = new Event('selectionchange', { bubbles: true });
      document.dispatchEvent(selectionEvent);
      
      // Attendre un peu pour voir si la toolbox appara√Æt
      await new Promise(resolve => setTimeout(resolve, 200));
      
      // V√©rifier si la toolbox est apparue
      const toolbox = document.querySelector('.linkedin-formatter-toolbox');
      const hasToolbox = toolbox && toolbox.style.display !== 'none';
      
      const result = {
        field: fieldData,
        testText: testText,
        hasToolbox: hasToolbox,
        toolboxPosition: hasToolbox ? toolbox.getBoundingClientRect() : null,
        timestamp: new Date().toISOString()
      };
      
      this.testedFields.push(result);
      
      console.log(`${hasToolbox ? '‚úÖ' : '‚ùå'} Test ${fieldData.description}: ${hasToolbox ? 'SUCC√àS' : '√âCHEC'}`);
      
      return result;
      
    } catch (error) {
      console.error(`‚ùå Erreur lors du test de ${fieldData.description}:`, error);
      return { field: fieldData, error: error.message };
    }
  }

  /**
   * S√©lectionne tout le texte dans un √©l√©ment
   */
  selectAllText(element) {
    if (element.contentEditable === 'true') {
      const range = document.createRange();
      range.selectNodeContents(element);
      const selection = window.getSelection();
      selection.removeAllRanges();
      selection.addRange(range);
    } else {
      element.select();
    }
  }

  /**
   * G√©n√®re un rapport d'analyse
   */
  generateReport() {
    console.log('\nüìä ====== RAPPORT D\'ANALYSE LINKEDIN ======');
    console.log(`üîç Analyse effectu√©e le: ${new Date().toLocaleString()}`);
    console.log(`üìÑ URL: ${window.location.href}`);
    console.log(`üìù Champs trouv√©s: ${this.foundFields.length}`);
    
    // Grouper par type
    const byType = {};
    this.foundFields.forEach(field => {
      if (!byType[field.type]) byType[field.type] = [];
      byType[field.type].push(field);
    });
    
    // Afficher par type
    Object.keys(byType).forEach(type => {
      console.log(`\nüìã === ${type.toUpperCase()} (${byType[type].length}) ===`);
      byType[type].forEach(field => {
        const visibility = field.isVisible ? 'üëÅÔ∏è' : 'üôà';
        console.log(`  ${visibility} ${field.description}`);
        console.log(`     S√©lecteur: ${field.selector}`);
        if (field.placeholder) {
          console.log(`     Placeholder: "${field.placeholder}"`);
        }
      });
    });
    
    // Recommandations
    console.log('\nüí° === RECOMMANDATIONS ===');
    const visibleFields = this.foundFields.filter(f => f.isVisible);
    const newFields = this.foundFields.filter(f => f.type === 'unknown' && f.isVisible);
    
    console.log(`‚úÖ Champs visibles: ${visibleFields.length}/${this.foundFields.length}`);
    console.log(`üÜï Nouveaux champs d√©couverts: ${newFields.length}`);
    
    if (newFields.length > 0) {
      console.log('\nüéØ Nouveaux s√©lecteurs √† ajouter:');
      newFields.forEach(field => {
        console.log(`  - ${field.selector} // ${field.description}`);
      });
    }
    
    console.log('\n====== FIN DU RAPPORT ======\n');
    
    return {
      total: this.foundFields.length,
      visible: visibleFields.length,
      newFields: newFields.length,
      byType: byType
    };
  }

  /**
   * Lance des tests automatiques sur tous les champs visibles
   */
  async runAutomaticTests() {
    console.log('üöÄ Lancement des tests automatiques...');
    
    const visibleFields = this.foundFields.filter(f => f.isVisible);
    
    for (const field of visibleFields) {
      await this.testFieldSelection(field);
      // Pause entre les tests
      await new Promise(resolve => setTimeout(resolve, 500));
    }
    
    console.log(`‚úÖ Tests termin√©s sur ${visibleFields.length} champs`);
    return this.testedFields;
  }
}

// Export pour utilisation dans content.js
window.LinkedInFieldAnalyzer = LinkedInFieldAnalyzer;

// Fonction globale pour lancer l'analyse facilement
window.analyzeLinkedInFields = async function() {
  const analyzer = new LinkedInFieldAnalyzer();
  await analyzer.analyzeCurrentPage();
  return analyzer;
};

// Fonction globale pour tester tous les champs
window.testAllLinkedInFields = async function() {
  const analyzer = new LinkedInFieldAnalyzer();
  await analyzer.analyzeCurrentPage();
  const results = await analyzer.runAutomaticTests();
  return results;
};

console.log('üîß LinkedIn Field Analyzer charg√©. Utilisez:');
console.log('   - analyzeLinkedInFields() pour analyser');
console.log('   - testAllLinkedInFields() pour tester');

export { LinkedInFieldAnalyzer };